html {
    font-family: "Montserrat", sans-serif;
}

#main {
    padding: 20px;
}

.title {
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 15px;
}

.subtitle {
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 15px;
    color: #969696;
}

p {
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 20px;
}

@media only screen and (min-width: 1024px) {
    #main {
        margin: 0 auto;
        max-width: 1000px;
        padding: 0 20px;
    }

    .title {
        font-size: 40px;
        margin-bottom: 20px;
    }

    .subtitle {
        font-size: 24px;
        margin-bottom: 30px;
    }

    p {
        font-size: 16px;
        line-height: 30px;
        margin-bottom: 20px;
    }
}
