.home-section {
        text-align: center;
        height: auto;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
    }

.home .intro-picture {
            display: block;
            max-width: 150px;
            border-radius: 100%;
            margin: 0 auto 20px;
        }

.home .intro .smiley {
            font-size: 22px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
                -ms-flex-direction: row;
                    flex-direction: row;
            -webkit-box-pack: center;
                -ms-flex-pack: center;
                    justify-content: center;
            margin-bottom: 20px;
        }

.home .intro .smiley span {
                padding: 0 4px;
            }

.home .intro .liveCounter {
            opacity: 0;
            -webkit-transition: 450ms ease-out;
            transition: 450ms ease-out;
            font-style: italic;
            color: #969696;
        }

.home .intro .liveCounter.active {
                opacity: 1;
            }

.home .nav-list {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            -webkit-box-pack: center;
                -ms-flex-pack: center;
                    justify-content: center;
        }

.home .nav-listItemIcon {
            display: block;
            width: 25px;
            height: 25px;
            margin-bottom: 12px;
        }

.home .nav-listItemIcon svg {
                fill: #141414;
            }

.home .nav-listItemLink {
            padding: 20px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
        }

.home .nav-listItemLink:hover .nav-listItemIcon svg {
                    fill: #969696;
                }

.home .nav-listItemLink:hover span {
                    color: #969696;
                }

@media only screen and (min-width: 1024px) {
        .home-section {
            height: 100vh;
        }
            .home .nav-list {
                -webkit-box-orient: horizontal;
                -webkit-box-direction: normal;
                    -ms-flex-direction: row;
                        flex-direction: row;
            }
}
